import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import CssBaseline from "@mui/material/CssBaseline";

const dispatchedEvents = [];

const SrsMuiCssBaseline = function (props, ref) {
  return <CssBaseline {...props} theme={props.muiTheme}>{props.content}</CssBaseline>;
};

const styles = `
  /* Custom styles for the MuiCssBaseline component */
`;

const props = [
  {
    name: "content",
    defaultValue: null,
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiCssBaseline),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
